@use '@angular/material' as mat;
@use './theme-colors' as _my-theme;
@use './variables' as _variables;

html {
  @include mat.theme(
    (
      color: (
        theme-type: light,
        primary: _my-theme.$primary-palette,
        tertiary: mat.$blue-palette,
      ),
      typography: (
        plain-family: Open Sans,
        brand-family: Montserrat,
      ),
      density: 0,
    )
  );
}

* {
  margin: 0px;
  box-sizing: border-box;
}

body {
  font-family: Montserrat;
  background-color: _variables.$global-light-gray-gray-2;
}

a {
  all: unset;
}

address {
  font-style: normal;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

// classes for typography
.body1 {
  font-size: _variables.$global-light-font-size-2;
  color: _variables.$global-light-gray-gray-9;
  font-family: _variables.$global-light-font-families-secondary;
}
.body2 {
  font-size: _variables.$global-light-font-size-1;
  color: _variables.$global-light-gray-gray-9;
  font-family: _variables.$global-light-font-families-secondary;
}
.body3 {
  font-size: _variables.$global-light-font-size-0;
  color: _variables.$global-light-gray-gray-9;
  font-family: _variables.$global-light-font-families-secondary;
}

h1 {
  font-weight: _variables.$global-light-font-weights-semibold;
  font-size: _variables.$global-light-font-size-8;
  color: _variables.$global-light-gray-gray-9;
}
h2 {
  font-weight: _variables.$global-light-font-weights-semibold;
  font-size: _variables.$global-light-font-size-7;
  color: _variables.$global-light-gray-gray-9;
}
h3 {
  font-weight: _variables.$global-light-font-weights-semibold;
  font-size: _variables.$global-light-font-size-6;
  color: _variables.$global-light-gray-gray-9;
}
h4 {
  font-weight: _variables.$global-light-font-weights-semibold;
  font-size: _variables.$global-light-font-size-5;
  color: _variables.$global-light-gray-gray-9;
}
h5 {
  font-weight: _variables.$global-light-font-weights-semibold;
  font-size: _variables.$global-light-font-size-4;
  color: _variables.$global-light-gray-gray-9;
}
h6 {
  font-weight: _variables.$global-light-font-weights-semibold;
  font-size: _variables.$global-light-font-size-3;
  color: _variables.$global-light-gray-gray-9;
}

// classes for font weight
.regular {
  font-weight: _variables.$global-light-font-weights-regular;
}
.semibold {
  font-weight: _variables.$global-light-font-weights-semibold;
}
.bold {
  font-weight: _variables.$global-light-font-weights-bold;
}

// classes for colors
.color-gray-1 {
  color: _variables.$global-light-gray-gray-1;
}
.color-gray-2 {
  color: _variables.$global-light-gray-gray-2;
}
.color-gray-3 {
  color: _variables.$global-light-gray-gray-3;
}
.color-gray-4 {
  color: _variables.$global-light-gray-gray-4;
}
.color-gray-5 {
  color: _variables.$global-light-gray-gray-5;
}
.color-gray-6 {
  color: _variables.$global-light-gray-gray-6;
}
.color-gray-7 {
  color: _variables.$global-light-gray-gray-7;
}
.color-gray-8 {
  color: _variables.$global-light-gray-gray-8;
}
.color-gray-9 {
  color: _variables.$global-light-gray-gray-9;
}
.color-gray-10 {
  color: _variables.$global-light-gray-gray-10;
}

.color-blue-1 {
  color: _variables.$global-light-daybreak-blue-blue-1;
}
.color-blue-2 {
  color: _variables.$global-light-daybreak-blue-blue-2;
}
.color-blue-3 {
  color: _variables.$global-light-daybreak-blue-blue-3;
}
.color-blue-4 {
  color: _variables.$global-light-daybreak-blue-blue-4;
}
.color-blue-5 {
  color: _variables.$global-light-daybreak-blue-blue-5;
}
.color-blue-6 {
  color: _variables.$global-light-daybreak-blue-blue-6;
}
.color-blue-7 {
  color: _variables.$global-light-daybreak-blue-blue-7;
}
.color-blue-8 {
  color: _variables.$global-light-daybreak-blue-blue-8;
}
.color-blue-9 {
  color: _variables.$global-light-daybreak-blue-blue-9;
}
.color-blue-10 {
  color: _variables.$global-light-daybreak-blue-blue-10;
}

//table styles
.table-ds {
  width: 100%;
  th {
    padding: _variables.$global-light-spacing-3;
    font-size: 0.875rem;
    font-family: _variables.$global-light-font-families-main;

    &:not(.table-numeric-align) {
      text-align: left;
    }
  }
  thead {
    background-color: _variables.$global-light-gray-gray-2;
  }
  td {
    padding: _variables.$global-light-spacing-3;
    font-family: 'Open Sans';
    color: _variables.$global-light-gray-gray-9;
  }
  .table-numeric-align {
    text-align: right;
  }
  @media (max-width: 420px) {
  }
}

.hidden {
  display: none;
}

//mat overrides

.grey-checkbox {
  @include mat.checkbox-overrides(
    (
      unselected-icon-color: _variables.$global-light-gray-gray-7,
    )
  );
}

:root {
  @include mat.form-field-overrides(
    (
      filled-container-color: _variables.$global-light-gray-gray-1,
      filled-container-shape: 8px,
      container-height: 60px,
      filled-label-text-weight: 400,
      container-text-weight: 400,
      filled-label-text-tracking: normal,
      filled-label-text-size: 0.875rem,
      filled-hover-label-text-color: _variables.$global-light-gray-gray-8,
      filled-label-text-color: _variables.$global-light-gray-gray-8,
      container-text-tracking: normal,
      filled-input-text-color: _variables.$global-light-gray-gray-9,
      filled-input-text-placeholder-color: _variables.$global-light-gray-gray-7,
      filled-disabled-container-color: _variables.$global-light-gray-gray-3,
    )
  );

  @include mat.select-overrides(
    (
      enabled-arrow-color: _variables.$global-light-gray-gray-7,
      panel-background-color: _variables.$global-light-gray-gray-1,
      // container-elevation-shadow: red,
    )
  );

  @include mat.dialog-overrides(
    (
      container-max-width: 80vw,
      container-shape: 12px,
    )
  );

  @include mat.tooltip-overrides(
    (
      container-color: _variables.$global-light-gray-gray-3,
      supporting-text-color: _variables.$global-light-gray-gray-8,
      supporting-text-font: _variables.$global-light-font-families-secondary,
      supporting-text-size: _variables.$global-light-font-size-0,
    )
  );
}

.btn-flat {
  @include mat.button-overrides(
    (
      filled-container-color: _variables.$global-light-daybreak-blue-blue-6,
      filled-label-text-weight: _variables.$global-light-font-weights-bold,
      filled-label-text-font: _variables.$global-light-font-families-main,
      filled-label-text-size: _variables.$global-light-font-size-2,
      filled-container-height: 60px,
      filled-container-shape: 12px,
      filled-disabled-container-color: _variables.$global-light-gray-gray-3,
      filled-disabled-label-text-color: _variables.$global-light-gray-gray-6,
    )
  );
}
.btn-flat.disabled {
  border: 1px solid _variables.$global-light-gray-gray-5;
}

.custom-btn-flat {
  @include mat.button-overrides(
    (
      filled-container-color: var(--primaryColor),
      filled-disabled-container-color: var(
          --secondaryColor3,
          _variables.$global-light-gray-gray-2
        ),
    )
  );
}

.radio-button-terms {
  @include mat.radio-overrides(
    (
      unselected-icon-color: _variables.$global-light-gray-gray-5,
      unselected-hover-icon-color: _variables.$global-light-gray-gray-6,
      selected-pressed-icon-color: _variables.$global-light-gray-gray-6,
      unselected-focus-icon-color: _variables.$global-light-gray-gray-6,
    )
  );
}

.checkbox-terms {
  @include mat.checkbox-overrides(
    (
      unselected-icon-color: _variables.$global-light-gray-gray-5,
      unselected-hover-icon-color: _variables.$global-light-gray-gray-6,
      selected-pressed-icon-color: _variables.$global-light-gray-gray-6,
      unselected-focus-icon-color: _variables.$global-light-gray-gray-6,
    )
  );
}

.mat-custom-form-field {
  @include mat.form-field-overrides(
    (
      filled-container-color: var(
          --secondaryColor5,
          _variables.$global-light-gray-gray-5
        ),
      filled-input-text-color: var(
          --neutralColor1,
          _variables.$global-light-gray-gray-1
        ),
      filled-label-text-color: var(--neutralColor5),
      filled-input-text-placeholder-color: var(--neutralColor6),
      filled-hover-label-text-color: var(--neutralColor4),
      filled-focus-label-text-color: var(--neutralColor5),
      filled-error-label-text-color: var(--alertColor1),
      filled-error-focus-active-indicator-color: var(--alertColor1),
      filled-error-focus-label-text-color: var(--alertColor1),
      filled-error-hover-label-text-color: var(--alertColor1),
    )
  );
  .mdc-text-field--filled:not(.mdc-text-field--disabled) {
    border-radius: 8px;
    border: 1px solid
      var(--secondaryColor3, _variables.$global-light-gray-gray-5);
  }
}

.btn-flat-secondary {
  @include mat.button-overrides(
    (
      filled-container-color: _variables.$global-light-gray-gray-2,
      filled-label-text-color: _variables.$global-light-gray-gray-8,
      filled-label-text-weight: _variables.$global-light-font-weights-bold,
      filled-label-text-font: _variables.$global-light-font-families-main,
      filled-label-text-size: _variables.$global-light-font-size-2,
      filled-container-height: 60px,
      filled-container-shape: 12px,
      filled-disabled-container-color: _variables.$global-light-gray-gray-3,
      filled-disabled-label-text-color: _variables.$global-light-gray-gray-6,
    )
  );
}

.custom-btn-flat-secondary {
  @include mat.button-overrides(
    (
      filled-label-text-color: var(
          --neutralColor1,
          _variables.$global-light-gray-gray-8
        ),
      filled-container-color: var(
          --secondaryColor3,
          _variables.$global-light-gray-gray-2
        ),
      filled-disabled-container-color: var(
          --secondaryColor3,
          _variables.$global-light-gray-gray-2
        ),
      filled-disabled-label-text-color: var(
          --neutralColor6,
          _variables.$global-light-gray-gray-6
        ),
    )
  );
}

//class to remove unnecessary border around button
.btn-flat-secondary.mat-mdc-unelevated-button:not(:disabled) {
  border: 0px;
}

.mdc-line-ripple {
  display: none;
}
.mdc-text-field--filled:not(.mdc-text-field--disabled) {
  border-radius: 8px;
  border: 1px solid _variables.$global-light-gray-gray-5;
}
.mdc-text-field--filled.mdc-text-field--disabled {
  border-radius: 8px;
  border: 1px solid _variables.$global-light-gray-gray-5;
}
.mat-mdc-form-field-focus-overlay {
  display: none;
}
.datepicker {
  .mat-mdc-icon-button {
    color: _variables.$global-light-gray-gray-6;
  }
}

.btn-outline {
  @include mat.button-overrides(
    (
      outlined-outline-color: _variables.$global-light-daybreak-blue-blue-6,
      outlined-label-text-color: _variables.$global-light-daybreak-blue-blue-6,
      outlined-disabled-outline-color: _variables.$global-light-gray-gray-5,
      outlined-disabled-label-text-color: _variables.$global-light-gray-gray-6,
      outlined-container-height: 60px,
      outlined-container-shape: 12px,
      outlined-label-text-font: _variables.$global-light-font-families-main,
      outlined-label-text-weight: _variables.$global-light-font-weights-semibold,
      outlined-label-text-size: _variables.$global-light-font-size-2,
    )
  );
}
.btn-outline-filled {
  @include mat.button-overrides(
    (
      filled-label-text-weight: _variables.$global-light-font-weights-semibold,
      filled-label-text-font: _variables.$global-light-font-families-main,
      filled-container-color: _variables.$global-light-gray-gray-1,
      filled-label-text-color: _variables.$global-light-daybreak-blue-blue-6,
    )
  );
}

.btn-outline-filled.btnDuesBG {
  @include mat.button-overrides(
    (
      filled-container-color: _variables.$global-light-daybreak-blue-blue-6,
      filled-label-text-color: _variables.$global-light-gray-gray-1,
    )
  );
}

.mat-mdc-unelevated-button:not(:disabled) {
  border: 1px solid _variables.$global-light-daybreak-blue-blue-6;
}

.custom-btn-flat.mat-mdc-unelevated-button:not(:disabled) {
  border: 1px solid var(--primaryColor);
}

.custom-checkbox {
  @include mat.checkbox-overrides(
    (
      unselected-icon-color: var(--neutralColor5),
      unselected-hover-icon-color: var(--neutralColor5),
      unselected-focus-icon-color: var(--neutralColor5),
    )
  );
}

